import React, { useEffect, useState } from 'react';
import { IUserInfo } from '../../../../interfaces/ApiInterfaces';
import { TOutputFormat } from '../utils/types/setupTypes';

type TProps = {
	userData?: IUserInfo;
	row: TOutputFormat;
};

export const RenderUserInfo = ({ userData, row }: TProps) => {
	const [data, setData] = useState('');
	useEffect(() => {
		if (userData) {
			//@ts-ignore
			setData(userData.info[row.content]);
		}
	}, [userData, row.content]);

	return <div style={{ display: 'inline-block' }}>{data}</div>;
};
